  @mixin xsm{
    @media(min-width: 434px){
      @content;
    }
  }

  @mixin sm{
    @media(min-width: 540px){
      @content;
    }
  }

  @mixin md{
    @media(min-width: 768px){
      @content;
    }
  }

  @mixin lg{
    @media(min-width: 992px){
      @content;
    }
  }
  
  @mixin xl{
    @media(min-width: 1200px){
      @content;
    }
  }

  @mixin xxl{
    @media(min-width: 1400px){
      @content;
    }
  }
  